import $ from "jquery";


class Alerta {

    alert(texto , bom){
        $(".alerta").hide();
        let alerta = "<div class='alerta'>"+texto+"</div>";
        $("body").append(alerta);
        if(bom){
            $(".alerta").css({
                "background-color":"rgb(75, 199, 102)",  
                "box-shadow":"inset 0px -.5em rgba(129, 136, 110, 0.623)",
            });
        }
        else{
            $(".alerta").css({
                "background-color":"tomato",
                "box-shadow":"inset 0px -.5em rgba(104, 69, 69, 0.623)",
            });    
        }
        $(".alerta").hide();
        setTimeout(()=>{$(".alerta").fadeIn("fast")}, 5);
        setTimeout(()=>{$(".alerta").fadeOut()}, 2500);



    }

}

export default new Alerta();$(this).hide();