import React , {Component} from 'react';
import "./header.css";
import logoCopy from "../../Image/copy.png";
import $ from "jquery";
import alerta from "../alerta";

class Header extends Component {
  constructor(props){
    super(props);
    this.state={
      nome:"",
      corp:"",
      tipo:"",
      cod:"1324",
    };

    this.alerta = this.alerta.bind(this);
  }
  alerta(text,pode){
    alerta.alert(text,pode);
  }


  render(){
    return (
      <div className="Header">
        <div class="userContainer">
          <div className="User"></div>
          <div class="boxCod">
            <div>FMM</div>
            <div className="cod">cod:<div className="codText">{this.state.cod}</div> <img className="copy" src={logoCopy}/></div>
          </div>
        </div>
        <div className="nome">{this.state.nome}</div>
        <div className="nomeCorp">{this.state.corp}</div>
        <div className="tipo">{this.state.tipo}</div>
      </div>

      
    );
 }

 componentDidMount(){
  

  $(".copy").click(function (){
    let text =$(".codText").text();
    let textField = document.createElement('textarea')
    textField.innerText = text;
    document.body.appendChild(textField);
    textField.select();
    document.execCommand('copy');
    textField.remove();

    alerta.alert(text , true);
  });


 }

}

export default Header;